.base-footer {
  padding-top: 20px;

  &__main {
    padding-bottom: 40px;
  }

  &__title {
    display: flex;
    align-items: center;
    height: 80px;
    padding: 25px 0 25px 20px;
  }

  &__logo {
    height: 100%;
  }

  &__content {
    padding: 0 20px 20px;
  }

  &__about {
    &-text,
    &-email {
      margin-bottom: 0;
      font-size:14px;
      font-weight:400;
      color:#9a9a9a;
      line-height:24px;
    }

    &-text {
      margin-top: 8px;
    }

    &-email {
      display: block;
      margin-top: 20px;

      .iconfont {
        margin-right: 8px;
      }

      &:hover {
        color: #2661E9;
        text-decoration: none;
      }
    }
  }

  &__links {
    &-item {
      font-size:14px;
      font-weight:400;
      color:#9a9a9a;
      line-height:30px;

      &:hover {
        color: #2661E9;
        text-decoration: none;
      }
    }
  }

  &__contact {
    &-icon {
      margin-top: 8px;
      font-size: 0;

      a {
        &:hover {
          text-decoration: none;
        }
      }

      .iconfont {
        margin-right: 20px;
        font-size: 30px;
        line-height: 30px;
        color: #9A9A9A;
        cursor: pointer;

        &:hover {
          color: #2661E9;
        }
      }
    }

    &-button {
      margin-top: 16px;
      color:#9a9a9a;
      border-color: #9A9A9A;

      &:hover {
        background: #2661E9;
        color: #FFFFFF;
        border-color: #2661E9;
      }
    }
  }

  &__country {
    &-tips {
      margin-top: 20px;
      font-size:12px;
      font-weight:400;
      color:#9a9a9a;
      line-height:24px;
    }
  }

  &__select {
    position: relative;
    width: 140px;

    select {
      appearance:none;
      -moz-appearance:none;
      -webkit-appearance:none;
      outline: none;
      padding-left: 12px;
      padding-right: 34px;
      background: #666;
      border-color: #666;
      color: #FFFFFF;

      &:focus,
      &:disabled {
        background: #666;
        border-color: #666;
        color: #FFFFFF;
      }
    }

    option {
      display: none;
    }

    &-icon {
      position: absolute;
      top: 50%;
      right: 12px;
      transform: translateY(-50%);
      color: #FFFFFF;
      font-size: 12px;
    }
  }

  &__dropdown {
    background: #666;
    border-color: #666;
    
    &-item {
      padding: 13.5px 12px;
      color: #FFFFFF;
    }
  }

  &__divider {
    width:100%;
    height:1px;
    background: #4B4B4B;
  }

  &__copyright {
    padding: 28px 0;
    font-size:12px;
    font-weight:400;
    color:#9a9a9a;
    line-height:24px;
    text-align: center;
  }
}