.page {
  &__banner {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 400px;

    &-title {
      max-width: 800px;
      margin-bottom: 0;
      font-size:60px;
      font-weight:bold;
      color:#FFFFFF;
      line-height: 1;
      text-align: center;
    }

    &-desc {
      max-width: 520px;
      margin-top: 20px;
      margin-bottom: 0;
      font-size:18px;
      font-weight:400;
      color:#FFFFFF;
      text-align: center;
    }
  }

  &__float {
    position: fixed;
    right: 30px;
    bottom: 30px;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }
}


.iconfont {
  line-height: 1;
}
