.base-header {
  font-size: 14px;

  &__brand {
    padding-top: 5px;
    padding-bottom: 10px;
  }

  &__logo {
    height: 30px;
  }

  &__dropdown {
    &-link {
      display: flex;
      align-items: baseline;
      
      &:after {
        display: none;
      }

      .iconfont {
        margin-left: 2px;
        font-size: 12px;
      }
    }

    &-menu {
      margin-top: 16px;
    }

    &-item {
      padding-top: 13.5px;
      padding-bottom: 13.5px;
    }
  }

  &__right {
    flex-direction: row;
  }

  &__button {
    color: #FFFFFF;
    border-color: #FFFFFF;
    &:hover {
      background: #F7931E;
      border-color: #F7931E;
      color: #ffffff;
    }

    &:focus {
      box-shadow: none;
    }

    &-link {
      &:hover { 
        color: #F7931E;
      }
    }
  }

  &__signin {
    color: #ffffff;
  }

  &__design {
    // margin-right: 20px;
  }

  &__avatar {
    width: 40px;
    height: 40px;
    margin: 4px 20px;
    border-radius: 50%;
    object-fit: contain;
    cursor: pointer;
  }
}

@media (max-width: 991px) {
  .base-header {
    &__dropdown {
  
      &-menu {
        margin-top: 0;
      }
    }

    &__button {
      display: block;
    }

    &__signin {
      padding-left: 0;
    }

    &__signup {
      margin-top: 16px !important;
    }

    &__avatar {
      margin-top: 16px;
      margin-left: 0;

      &-dropdown {
        width: 100%;
      }
    }
  }
}

@media (max-width: 575px) {
  .base-header {
    &__nav {
      padding-left: 20px;
      padding-right: 20px;
    }
  }
}